.HomePage {
  position: fixed;
  height: 100%;
}

.SuburbQuickView {
  position: fixed;
  top: 10px; right: 10px;
  z-index: 1000;
}

.Legend {
  position: absolute;
  bottom: 20px; left: 10px;
  z-index: 1000;
}

.SuburbSearch {
  position: absolute;
  top: 0; left: 0;
  z-index: 1000;
}

.HomePage__cta-btn {
  position: absolute;
  bottom: 20px; left: 50%;
  transform: translateX(-50%);
  z-index: 1000;

  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 35px;
  text-align: center;
  font-weight: bold;
  color: white;
  background-color: var(--primary-red-color);
  padding: 8px 10px;
  border-radius: 2px;
  text-decoration: none;
}

@media (max-width: 768px) {
  .SuburbQuickView {
    top: 70px;
  }
}
