:root {
  --side-padding: 60px;
}

.App {
  /*
  position: fixed;
  top: 0; left: 0;
  display: flex;
  height: 100%; width: 100%;*/
}

.Page {
  width: 100%;
  padding-top: 80px;
  /*overflow-y: auto;*/
}

.Page--no-top-space {
  padding-top: 0;
}

.Page__content {
  box-sizing: border-box;
  width: calc(100% - var(--side-padding) * 2);
  max-width: 1024px;
  margin: 0 auto;
  padding-bottom: 50px;
}

.Page__title {
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  :root {
    --side-padding: 20px;
  }
}
